// basic functionalty for the skip link
.skip-link {
    position: absolute;
    display: inline-block;
    top: -1000px;
    left: -1000px;

    &:active,
    &:focus,
    &:hover {
        top: 0;
        left: 0;
        z-index: 200;
    }
}

// theming for the skip link
.skip-link {
    padding: map-get($skip, padding);
    background: map-get($skip, background);
    color: map-get($skip, color);
}

