//
// These are the key variables for styling the popover.
// Just set the variable to none if you don't want that styling.

// KEY VARIABLES
// =============================================================================

// STYLES
$popover-width: 30em !default;

// Ideal width of the popover
$popover-max-width: 100% !default;

// Best as a % to accommodate smaller viewports
$popover-max-height: 15em !default;

// Maximum size of the content area
$popover-color-background: rgb(255, 255, 255) !default;

// Color of the popover background
$popover-border-radius: 0;

// Radius of the corners of the popover
$popover-border: 1px solid rgb(195, 195, 195) !default;

// Border of the popover/ tooltip
$popover-inactive-opacity: 0 !default;

// Opacity of the popover when instantiated/ deactivating
$popover-active-opacity: 0.97 !default;

// Opacity of the popover when active
$popover-box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.3) !default;

// Sets the box shadow under the popover/ tooltip
$popover-bottom-position: auto !default;

// Sets the bottom position of the popover. Use only when setting positionPopover to false in the script
$popover-left-position: auto !default;

// Sets the left position of the popover. Use only when setting positionPopover to false in the script
$popover-tooltip-size: 1.3em !default;

// Sets the side lengths of the tooltip
$popover-scroll-indicator-width: 0.625em !default;

// The width of the scroll indicator
$popover-scroll-indicator-aspect-ratio: 15 / 12 !default;

// The ratio of the height over the width of the scroll indicator
$popover-scroll-indicator-opacity: 0.1 !default;

// The active opacity of scroll indicators
$popover-initial-transform-state: scale(0.1) translateZ(0) !default;

// The inital transform state for the popover
$popover-active-transform-state: scale(1) translateZ(0) !default;

// The transform state for the popover once it is fully activated

// OPTIONAL ELEMENTS
$popover-include-tooltip: true !default;

// Adds a tooltip pointing to the footnote button
$popover-include-scroll-indicator: true !default;

// Adds an elipsis at the bottom of scrollable popovers
$popover-include-scrolly-fades: true !default;

// Fades content in on scrollable popovers
$popover-scroll-indicator-icon: url("data:image/svg+xml;base64,") !default;

// OTHER VARIABLES
// =============================================================================

// POPOVER
$popover-margin-top: 0.1em !default;
$popover-padding-content-horizontal: 1.3em !default;
$popover-padding-content-top: 1.1em !default;
$popover-padding-content-bottom: 1.2em !default;
$popover-z-index: 10 !default;

// Set the base so that it's above the other body children
$popover-initial-transform-origin: 50% 0 !default;

// POPOVER CONTENT WRAPPER
$popover-content-color-background: $popover-color-background !default;
$popover-content-border-radius: $popover-border-radius !default;

// OTHER POPOVER ELEMENTS
$popover-tooltip-background: $popover-color-background !default;
$popover-tooltip-radius: 0 !default;
$popover-scroll-indicator-bottom-position: 0.45em !default;
$popover-scrolly-fade-gradient-start-location: 50% !default;
$popover-scroll-indicator-padding: $popover-padding-content-horizontal / 2 - $popover-scroll-indicator-width / 2 !default;

// TRANSITIONS
$popover-transition-default-duration: 0.25s !default;
$popover-scroll-indicator-transition-properties: opacity !default;

// Use none for areas you don't want to transition
$popover-transition-properties: opacity, transform !default;

// no mixin to do proper prefixing of the transform, so I have to do it manually; see mixin below
$popover-scroll-indicator-transition-properties: opacity !default;
$popover-scroll-up-transition-delay: 0.4s !default;

// Sets the delay for the transition of the scroll indicator when scrolling upwards
$popover-transition-default-timing-function: ease !default;

