@mixin print-styles {
  // These styles restore the original footnote numbers and texts when the page is printed
  @media not print {
    .footnote-print-only {
      display: none !important;
    }
  }

  @media print {
    .bigfoot-footnote,
    .bigfoot-footnote__button {
      display: none !important;
    }
  }
}

